
.content {
  position: relative;
  max-width: 100em;
  top:3rem;
    z-index: 10;

    display: grid;
    align-items: center;
    gap: 5em;
  /*  display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 20px; /* Define the gap between grid items */
    margin-bottom: 30vh;
  }
  
  .content-block {
    height:30em;
    width:90%;

    z-index: 10;
    
    display: grid;
  grid-template-columns: 20em 1fr;
  }

  
  .image{
    width:20em;
    height:inherit;
    overflow: hidden;
    border-radius: 20px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.13);
  }


  .text{
    width:60%;
    margin:15%;
    margin-left: 20%;
    text-align: center;
    overflow: hidden;
  }
  
  .title{
    width:100%;
    position: relative;
    top:50px;
    text-align: center;
  }


  .image img {
    height: 35em;
    transform: translateX(-12em);

  }
  
         
  .navbar-background{

    background-color:rgba(240, 240, 240, 0.9);
    z-index: 15;
    position: fixed;

    border-radius: 0.4em;
    box-shadow: 0 4px 40px rgba(0, 0, 0, 0.13);
  }

  .navbar-background,
  body::before {
    height: 35vh;
    width:45%;
    bottom:-100px;
    right: 10%;
    position: fixed;
  }




  body::before {
    content: "";
    backdrop-filter: blur(2px); /* Adjust blur amount as needed */
    z-index: 10; /* Make sure this is below the .navbar-background */
  }

