/* LandingPage.css */

    /* Styles for the navigation bar container */
    .landing-page {
        justify-content: center;
        align-items: center;
        height: 100vh; /* Adjust the height as needed */
        background-image: linear-gradient(to right, #F6F6F6, #F0F0F0);
        max-width: 100%;
        overflow-x:hidden;
    }

      .nameplate{
        position: absolute;
        top:64%;
        width:70%;
        left:10%;
        display: grid;
        place-items: center;
      }

      .logoimg{
        position: absolute;
        left:11%;
        height:100px;   
        stroke-width: 100px;
      }